import Vue from 'vue'
import VueRouter from 'vue-router'
//import Home from '../views/Home.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import('@/views/layout/Index.vue'),
    children: [
      {
        path: '/',
        name: 'Home',
        meta: { authRequired: true },
        //component: Home
        component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue')
      },
      {
        path: '/one',
        name: 'One',
        component: () => import(/* webpackChunkName: "one" */ '../views/One.vue')
      },
      {
        path: '/two',
        name: 'Two',
        component: () => import(/* webpackChunkName: "two" */ '../views/Two.vue')
      },
      {
        path: '/notfound',
        name: 'NotFound',
        meta: { authRequired: true },
        component: () => import(/* webpackChunkName: "notfound" */ '../views/NotFound.vue'),
    
      },
      {
        path: '/admin',
        name: 'Admin',
        meta: { authRequired: true },
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Admin.vue')
      },
    ],
  },
  {
    path: '/auth',
    name: 'Auth',
    component: () => import('@/views/layout/LoginLayout.vue'),
    children: [
      {
        path: '/login',
        name: 'Login',
        component: () => import('@/views/Login.vue')
      }
    ],
    meta: { authRequired: false }
  },
  // {
  //   path: '/login',
  //   name: 'Login',
  //   meta: { authRequired: false },
  //   component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue'),

  // },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.authRequired) && !store.getters.isAuth) {
    alert('please login ');
    next({ path: '/login', replace: true })
  } else {
    next()
  }
})

export default router
