import Vue from 'vue'
import Vuex from 'vuex'

//import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: '',
    count: 0,
    myIp: ''
  },
  getters: {
    isAuth: state => {
      return !!state.token
    },
  },
  mutations: {
    increment(state, n) {
      state.count += n
    },
    setIP(state, ip) {
      state.myIp = ip 
    },
    login(state, token) {
      console.log(token)
      if (!token) return
      state.token = token
      localStorage.setItem('token', token)
    },
    logout(state) {
      state.token = null
      localStorage.removeItem('token')
    },
  },
  actions: {
    login({ commit }, { token }) {
      commit('login', token)
    },
    logout(state) {
      state.token = null
      localStorage.removeItem('token')
    },
    // async getIP({ commit }) {
    //   console.log(this)
    //   await axios.get('http://icanhazip.com').then((res) => {
    //     commit('setIP', res.data)
    //   }) 
      
    // }
  },
  modules: {
  }
})
